import { useNavigate } from 'react-router-dom';
import { App } from '../types';

interface CatalogCardProps {
  item: App;
}

export const CatalogCard: React.FC<CatalogCardProps> = ({ item }) => {
  const navigate = useNavigate();

  const handleTelegramClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    window.open(item.telegramLink, '_blank');
  };

  const handleCardClick = () => {
    navigate(`/app/${item.id}`);
  };

  return (
    <div 
      className="bg-telegram-bg-primary rounded-lg p-4 shadow-sm hover:shadow-md transition-shadow cursor-pointer"
      onClick={handleCardClick}
    >
      <div className="flex items-start space-x-4">
        <div className="flex-shrink-0 w-12 h-12 flex items-center justify-center bg-telegram-primary rounded-lg text-2xl">
          {item.icon}
        </div>
        <div className="flex-1 min-w-0">
          <h3 className="text-lg font-medium text-telegram-text-primary truncate">
            {item.name}
          </h3>
          <p className="text-sm text-telegram-text-secondary line-clamp-2">
            {item.description}
          </p>
          <div className="mt-2 flex flex-wrap gap-2">
            {item.tags.map((tag) => (
              <span
                key={tag}
                className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-telegram-primary/10 text-telegram-primary"
              >
                {tag}
              </span>
            ))}
          </div>
        </div>
      </div>

      <div className="mt-4 space-y-3">
        <div>
          <h4 className="text-sm font-medium text-telegram-text-primary mb-2">:</h4>
          <ul className="list-disc list-inside text-sm text-telegram-text-secondary">
            {item.features.map((feature, index) => (
              <li key={index}>{feature}</li>
            ))}
          </ul>
        </div>
        <button
          onClick={handleTelegramClick}
          className="w-full py-2 px-4 bg-telegram-primary text-telegram-text-light rounded-lg hover:bg-telegram-secondary transition-colors"
        >
            Telegram
        </button>
      </div>
    </div>
  );
}; 