import { useNavigate, useParams } from 'react-router-dom';
import { useEffect, useState } from 'react';
import { doc, getDoc } from 'firebase/firestore';
import { db } from '../firebase/config';
import { App } from '../types';

const AppDetails = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [app, setApp] = useState<App | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchApp = async () => {
      if (!id) return;
      
      try {
        const docRef = doc(db, 'apps', id);
        const docSnap = await getDoc(docRef);
        
        if (docSnap.exists()) {
          setApp({ id: docSnap.id, ...docSnap.data() } as App);
        }
      } catch (error) {
        console.error('Error fetching app:', error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchApp();
  }, [id]);

  const handleTelegramClick = () => {
    if (app?.telegramLink) {
      window.open(app.telegramLink, '_blank');
    }
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-telegram-primary"></div>
      </div>
    );
  }

  if (!app) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold mb-4">  </h2>
          <button
            onClick={() => navigate('/')}
            className="px-4 py-2 bg-telegram-primary text-white rounded hover:bg-telegram-secondary"
          >
             
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-telegram-bg-primary">
      <div className="container mx-auto px-4 py-6">
        <button
          onClick={() => navigate('/')}
          className="mb-6 flex items-center text-telegram-text-primary hover:text-telegram-primary"
        >
          <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
          </svg>
          
        </button>

        <div className="bg-telegram-bg-primary rounded-lg p-6 shadow-sm">
          <div className="flex items-start space-x-4 mb-6">
            <div className="flex-shrink-0 w-16 h-16 flex items-center justify-center bg-telegram-primary rounded-lg text-4xl">
              {app.icon}
            </div>
            <div className="flex-1">
              <h1 className="text-2xl font-bold text-telegram-text-primary mb-2">
                {app.name}
              </h1>
              <p className="text-telegram-text-secondary">
                {app.description}
              </p>
            </div>
          </div>

          <div className="mb-6">
            <h2 className="text-lg font-semibold text-telegram-text-primary mb-3">
              
            </h2>
            <ul className="list-disc list-inside space-y-2 text-telegram-text-secondary">
              {app.features.map((feature, index) => (
                <li key={index}>{feature}</li>
              ))}
            </ul>
          </div>

          <div className="mb-6">
            <h2 className="text-lg font-semibold text-telegram-text-primary mb-3">
              
            </h2>
            <div className="flex flex-wrap gap-2">
              {app.tags.map((tag, index) => (
                <span
                  key={index}
                  className="px-3 py-1 bg-telegram-primary/10 text-telegram-primary rounded-full text-sm"
                >
                  {tag}
                </span>
              ))}
            </div>
          </div>

          <button
            onClick={handleTelegramClick}
            className="w-full py-3 px-4 bg-telegram-primary text-telegram-text-light rounded-lg hover:bg-telegram-secondary transition-colors"
          >
              Telegram
          </button>
        </div>
      </div>
    </div>
  );
};

export default AppDetails; 