import React, { useState, useEffect } from 'react';
import { collection, getDocs, addDoc, deleteDoc, doc } from 'firebase/firestore';
import { db } from '../firebase/config';
import { App } from '../types';

const Admin = () => {
  const [apps, setApps] = useState<App[]>([]);
  const [newApp, setNewApp] = useState<Partial<App>>({
    name: '',
    description: '',
    icon: '',
    telegramLink: '',
    category: 'bots',
    tags: [],
    features: []
  });

  useEffect(() => {
    fetchApps();
  }, []);

  const fetchApps = async () => {
    try {
      const appsCollection = collection(db, 'apps');
      const appsSnapshot = await getDocs(appsCollection);
      const appsData = appsSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      })) as App[];
      setApps(appsData);
    } catch (error) {
      console.error('Error fetching apps:', error);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const appsCollection = collection(db, 'apps');
      await addDoc(appsCollection, newApp);
      setNewApp({
        name: '',
        description: '',
        icon: '',
        telegramLink: '',
        category: 'bots',
        tags: [],
        features: []
      });
      fetchApps();
    } catch (error) {
      console.error('Error adding app:', error);
    }
  };

  const handleDelete = async (id: string) => {
    try {
      await deleteDoc(doc(db, 'apps', id));
      setApps(apps.filter(app => app.id !== id));
    } catch (error) {
      console.error('Error deleting app:', error);
    }
  };

  return (
    <div className="container mx-auto px-4 py-6">
      <h2 className="text-2xl font-bold mb-6">-</h2>
      
      <form onSubmit={handleSubmit} className="mb-8">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium mb-1"></label>
            <input
              type="text"
              value={newApp.name}
              onChange={(e) => setNewApp({ ...newApp, name: e.target.value })}
              className="w-full p-2 border rounded"
              required
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-1"></label>
            <input
              type="text"
              value={newApp.description}
              onChange={(e) => setNewApp({ ...newApp, description: e.target.value })}
              className="w-full p-2 border rounded"
              required
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-1"></label>
            <input
              type="text"
              value={newApp.icon}
              onChange={(e) => setNewApp({ ...newApp, icon: e.target.value })}
              className="w-full p-2 border rounded"
              required
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-1"> Telegram</label>
            <input
              type="text"
              value={newApp.telegramLink}
              onChange={(e) => setNewApp({ ...newApp, telegramLink: e.target.value })}
              className="w-full p-2 border rounded"
              required
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-1"></label>
            <select
              value={newApp.category}
              onChange={(e) => setNewApp({ ...newApp, category: e.target.value as 'bots' | 'channels' | 'apps' })}
              className="w-full p-2 border rounded"
              required
            >
              <option value="bots"></option>
              <option value="channels"></option>
              <option value="apps"></option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium mb-1"> ( )</label>
            <input
              type="text"
              value={newApp.tags?.join(', ')}
              onChange={(e) => setNewApp({ ...newApp, tags: e.target.value.split(',').map(tag => tag.trim()) })}
              className="w-full p-2 border rounded"
            />
          </div>
          <div className="md:col-span-2">
            <label className="block text-sm font-medium mb-1"> (   )</label>
            <textarea
              value={newApp.features?.join('\n')}
              onChange={(e) => setNewApp({ ...newApp, features: e.target.value.split('\n').map(feature => feature.trim()) })}
              className="w-full p-2 border rounded h-24"
            />
          </div>
        </div>
        <button
          type="submit"
          className="mt-4 px-4 py-2 bg-telegram-primary text-white rounded hover:bg-telegram-secondary"
        >
           
        </button>
      </form>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {apps.map((app) => (
          <div key={app.id} className="border rounded p-4">
            <div className="flex justify-between items-start">
              <div>
                <h3 className="text-lg font-medium">{app.name}</h3>
                <p className="text-sm text-gray-600">{app.description}</p>
              </div>
              <button
                onClick={() => handleDelete(app.id)}
                className="text-red-500 hover:text-red-700"
              >
                
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Admin; 